.video_container .player_container {
  position: relative;
  width: 1280px;
  margin: 40px auto 40px auto;
  box-shadow: 0px 2px 15px 0px rgba(0, 0, 0, 0.38);
  border-radius: 25px;
  overflow: hidden;
}

@media (max-width: 1500px) {
  .video_container .player_container {
    width: 853px;
  }
}

@media (max-width: 1000px) {
  .video_container .player_container {
    width: 640px;
  }
}

@media (max-width: 700px) {
  .video_container .player_container {
    width: 426px;
    border-radius: 15px;
  }
}

@media (max-width: 450px) {
  .video_container .player_container {
    width: 336px;
  }
}

.video_container .player_container video {
  border-radius: 25px;
}

@media (max-width: 700px) {
  .video_container .player_container video {
    border-radius: 15px;
  }
}

.video_container .player_container .loader {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #b5d6ff;
}

.video_container .player_container .loader .lds-ring {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.video_container .player_container .loader .lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid red;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: red transparent transparent transparent;
}

.video_container .player_container .loader .lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}

.video_container .player_container .loader .lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}

.video_container .player_container .loader .lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}

@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/*# sourceMappingURL=VideoContainer.css.map */
.svg {
  position: absolute;
}

.svg:hover {
  cursor: pointer;
}

.animate__animated.animate__bounceInDown {
  --animate-duration: .7s;
}
